html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5 {
  margin: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Josefin Sans", sans-serif;
}

li {
  list-style: none;
}

button, input[type=checkbox] {
  cursor: pointer;
  border: none;
}

.completed {
  text-decoration: line-through;
  opacity: 0.5;
}

.todo__form {
  margin-bottom: 20px;
}
.todo__form input {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.21);
  border-radius: 5px;
  background: white;
  border-color: transparent;
  width: 80%;
  padding: 0.9375rem 0rem 0.9375rem 1.1875rem;
  outline: none;
}
.todo__form input:focus {
  border: 2px #66b5ff solid;
}
.todo__form input::placeholder {
  opacity: 0.5;
  font-style: italic;
}
.todo__item {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.21);
  background: white;
  border: none;
  padding: 0.9375rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.todo__item li {
  margin-left: 3rem;
}
.todo__button--check {
  position: absolute;
}
.todo__button--delete {
  background: #f72684;
  color: white;
  font-weight: bold;
  border-radius: 3px;
  padding: 0.25rem 0.4375rem;
}